@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600;700&display=swap);
.loading-screen {
    text-align: center;
    width: 100vw;
    height: 100vh;
}

.loading-screen h1 {
    margin-top: 30vh;
    font-weight: 700;
    font-size: 3em !important;
    font-family: 'Arial' !important;
}

.loader {
    margin-left: auto;
    margin-right: auto;
    margin-top: 2em;
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #1dbc98;
    border-radius: 50%;
    width: 8em;
    height: 8em;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

@media (min-width: 1050px) {
    #title {
        font-size: 3em !important;
    }

    h1 {
        font-size: 2em;
    }

    h2 {
        font-size: 1.5em !important;
        line-height: 1.6em !important;
    }

    h3 {
        font-size: 1.2em !important;
    }

    p {
        font-size: 1em !important;
        line-height: 1.6em !important;
    }
}

body {
    margin: 0;
    font-family: 'Open Sans', serif, -apple-system, BlinkMacSystemFont,
        'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
        'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c2c2f;
}

.content {
    animation: fadeInAnimation ease-in 0.7s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

/* navbar */
.nav-logo {
    width: 10em;
}

.footer-logo {
    width: 15em;
}

.custom-nav {
    font-size: 0.875em;
    background-color: white;
    padding: 1em 10% 1em 1em;
}

.nav-links {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.nav-tab {
    border-bottom: solid 4px #e7e7e7;
    color: #2c2c2f !important;
    text-decoration: none;
    padding-left: 2em !important;
    padding-right: 2em !important;
    font-weight: 600;
}

.nav-active {
    border-bottom: solid 4px #f07545;
}

.nav-dropdown {
    padding-left: 2em;
    padding-right: 2em;
    margin-bottom: 0;
    margin-top: 1em;
}

.dropdown-menu {
    margin-top: 0.5em !important;
}

#basic-nav-dropdown {
    color: #2c2c2f !important;
}

.nav-btn {
    margin: auto 2em;
}

.dropdown-item:active {
    background-color: #f07545 !important;
}

/* landing page */
#landing {
    background: url(/static/media/landing.7faa78e5.png) no-repeat center center;
    background-size: cover;
    height: 60vh;
    margin-top: 0;
    width: auto;
    text-align: right;
    padding: 10% 10vw;
}

#welcome-txt {
    text-align: left;
    font-size: 1.5em;
    margin: 0;
}

.landing-boxes-small {
    height: 6vw;
}

.landing-boxes {
    height: 7vw;
}

#identity-div {
    text-align: right;
}

#identity-txt {
    font-weight: 600;
    width: 100%;
}

.landing-content-R {
    display: inline-block;
    text-align: right;
    padding-left: 0;
    left: 0;
    color: white;
}

.landing-btn {
    background: none;
    border: none;
    color: white;
    font-size: 1.2em;
    font-weight: 600;
    text-decoration: underline;
}

.landing-btn:hover {
    color: white;
    font-weight: 700;
    outline: none;
}

.landing-btn:active {
    outline: none;
}

.landing-btn:focus {
    outline: none;
}

#title {
    font-weight: 600;
    margin: 0;
}

/* service landing section */
#service-landing {
    background-color: #f8f8f9;
    height: 24vh;
}

.service-box {
    background-color: white;
    margin: -10vh 1em;
    width: 25vw;
    text-align: center;
    padding: 2em 1em 1em 1em;
    box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
}

.service-box p {
    color: #727574;
}

/* who we are */
#who-we-are {
    padding-top: 8em;
    padding-bottom: 8em;
    background: url(/static/media/who-we-are.dbd45040.png) no-repeat center center;
    background-size: cover;
    color: #ffffff;
    text-align: center;
}

#who-we-are h1 {
    font-weight: 700;
}

#who-we-are-text {
    width: 60vw;
    line-height: 1.2em;
    margin: 2em auto 2em auto;
}

/* service icons */
#services-icons-section {
    text-align: center;
    padding: 5% 10%;
    background-color: #f8f8f9;
}

#services-icons-section h2 {
    font-weight: 700;
    margin-bottom: 2em;
}

.services-icons {
    width: 3em;
}

.service-card {
    margin-top: 1em;
    margin-bottom: 1em;
    text-align: left;
}

.service-card h3 {
    font-weight: bold;
    margin-top: 1em;
    margin-bottom: 0.5em;
}

/* footer */
#footer {
    background-color: #f1f2f4;
    margin: 0;
    padding: 2em 4em 1em 4em;
    z-index: 2 !important;
}

#footer .col {
    margin-bottom: 2em;
}

#address-footer {
    margin-top: 2em;
    text-align: center;
    border-top: solid 1px #bfbfbf;
    padding-top: 2em;
}

#footer h3 {
    font-weight: 700;
    margin-bottom: 1em;
}

#footer a {
    color: #2c2c2f;
}

.footer-link {
    margin-bottom: 0.5em;
}

.contact-item {
    margin-left: 0.5em;
}

.contact-img {
    margin-top: 0.2em;
    height: 1.5em;
}

.contacts {
    width: 30%;
}
/* about page */

.about-header {
    background: url(/static/media/about-banner.dc4f952c.png) no-repeat center center;
}

.about-content {
    background-color: #f8f8f9;
    padding-bottom: 10em;
    margin-top: 0;
}

.about-content h2 {
    font-weight: 600;
    margin-bottom: 1em;
}

.about-text p {
    width: 60vw;
    text-align: center;
    margin: 10vh auto;
}

.about-vision-image {
    width: 100%;
    background: url(/static/media/about-img-1.0477063b.jpg) no-repeat center center;
}

.about-img {
    margin-bottom: 4em;
    width: 100%;
    background: url(/static/media/about-img-2.bfb5fb07.jpg) no-repeat center center;
}

.about-vision-image h1 {
    font-weight: 700;
}

.about-img img {
    max-width: 100%;
}

.about-content-sub {
    margin-bottom: 6em;
}

/* philosophy section */
#phil-section {
    display: flex;
    background-color: #f1f2f4;
    padding-bottom: 2em;
}

#phil-box {
    margin: -15vh 10% 0 10%;
    padding: 2em;
    width: 100%;
    background-color: #ffffff;
    box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
}

#phil-title {
    font-weight: 600;
    border-bottom: solid 1px #e7e7e7;
    padding-bottom: 0.5em;
    width: 100%;
}

#phil-box .col {
    padding: 2em;
}

#phil-box .col:nth-child(1),
#phil-box .col:nth-child(2) {
    border-right: solid 1px #e7e7e7;
}

#phil-box h3 {
    margin-top: 2em;
    font-weight: 600;
}

/* commitments section */

#commitments {
    padding: 5%;
    background-color: #f8f8f9;
}

#commitments h1 {
    font-weight: 600;
    margin-bottom: 1em;
}

.value {
    color: #1dbc98;
    font-weight: 700;
    margin: 1em 2em;
    padding: 1em 5em;
    background-color: #ffffff;
    box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
    width: 25vw;
}

/* get quote box */
.service-page-box {
    background-color: white;
    box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.1);
    width: 320px;
    min-width: 70%;
    margin: 0 auto;
}

.service-box-active p {
    color: #1dbc98;
    font-weight: 700;
}

.service-box-active a:hover {
    text-decoration: none !important;
}

#service-page-nav a {
    color: #2c2c2f;
}

#service-page-nav p:hover {
    background-color: #f8f8f9;
}

#service-page-nav a:hover {
    text-decoration: none;
}

.service-page-box h3 {
    text-align: center;
    font-weight: 600;
    background-color: #1dbc98;
    padding: 1em;
    margin-bottom: 2px;
    color: #ffffff;
}

.service-page-content {
    padding: 0.5em 1em 1em 1em;
}

.service-page-btn {
    display: block;
    margin-top: 1em;
}

.service-page-nav-item {
    padding: 1em;
    margin: 0 1em;
}

li p {
    margin: 0.5em !important;
}

.service-page-nav-item:nth-child(1),
.service-page-nav-item:nth-child(2) {
    border-bottom: solid 1px #e7e7e7;
}

/* shipping service page */
.service-page {
    background-color: #f8f8f9;
    padding: 5%;
}

.service-R {
    width: 70%;
    padding: 0 4em;
}

.service-R h2 {
    font-weight: 600;
}

.service-R .row {
    margin-top: 2em;
}

.service-R .col:nth-child(1) {
    margin-right: 4em;
}

.service-L {
    width: 30%;
}

.service-R h3 {
    font-weight: 600;
    margin-bottom: 1em;
}

.shipping-header {
    background: url(/static/media/shipping-banner.2ac7d9d5.png) no-repeat center center;
}

.warehouse-header {
    background: url(/static/media/warehouse-banner.033c9162.png) no-repeat center center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
}

.warehouse-page-img {
    width: 20em;
}

.ocean-header {
    background: url(/static/media/ocean-banner.efd9b226.png) no-repeat center center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.4);
}

.airfreight-header {
    background: url(/static/media/airfreight-banner.dedc35ad.png) no-repeat center center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
}

.header-img {
    height: 30vh;
    padding-top: 10vh;
    background-size: cover;
    justify-content: center;
}

.header {
    color: #ffffff;
    font-weight: 700;
}

/* form submit */

#form-submit-pg {
    background-color: #f8f8f9;
    text-align: center;
    width: 100%;
    margin: 0;
    padding: 4em;
    height: 50vh;
}

.theme-btn {
    text-align: center;
    font-size: 1em;
    outline: none;
    background-size: 100% 200%;
    background-image: linear-gradient(to top, #f07545 50%, #1dbc98 50%);
    transition: background-position 0.2s;
    padding: 0.8em 2em;
    border: none;
    color: white;
    font-weight: 600;
    border-radius: 0.2em;
}

.theme-btn:hover {
    color: white;
    font-weight: 700;
    text-decoration: none;
    background-position: 0 100%;
}

/* ocean shipping */
.sub-page-icon-text {
    margin-top: 2em;
    font-weight: 600;
}

/* form toggle */
.form-toggle {
    position: fixed;
    bottom: 0;
    right: 0;
    margin: 5%;
    text-align: right;
    z-index: 100000;
}

@keyframes slide-form {
    from {
        padding: 0;
        height: 0;
        overflow: hidden;
    }
    to {
        height: 500px;
        overflow: hidden;
    }
}

.form-toggle form {
    max-height: 70vh;
    overflow: scroll;
    background-color: #ffffff;
    padding: 2em;
    margin-bottom: 2em;
    border-radius: 4px;
    box-shadow: 5px 5px 15px 0 rgba(0, 0, 0, 0.2);
    width: 1000px;
    max-width: 80vw;
    text-align: left;
    animation-name: slide-form;
    animation-duration: 0.2s;
}

.form-toggle form p {
    margin-bottom: 2em;
}
.form-toggle-chat {
    border-radius: 50%;
    background-color: #1dbc98;
    width: 4.5em;
    height: 4.5em;
    border: none;
    outline: none;
    text-align: center;
    vertical-align: middle;
    box-shadow: 5px 5px 15px 0 rgba(0, 0, 0, 0.2);
}

.form-toggle-chat:focus {
    border: none;
    outline: none;
}
.form-toggle-icon {
    width: 2.2em;
    padding-top: 0.5em;
}

.how-it-works {
    padding-top: 1em;
    padding-bottom: 2em;
}

.how-it-works h2 {
    font-weight: 700;
}

.how-it-works-numbers {
    color: #1dbc98;
}
.how-it-works h3 {
    font-weight: 700;
}

/* ipad */
