    }

    h3 {
        font-size: 0.9em !important;
    }

    p {
        font-size: 0.7em !important;
    }

    label {
        font-size: 0.8em;
    }

    .custom-nav {
        font-size: 0.6em !important;
        background-color: white;
        padding: 0.5em !important;
        padding-right: 10% !important;
    }

    .dropdown-item {
        font-size: 0.7em !important;
    }

    .footer-logo {
