    }

    .service-page-box {
        margin: 0 !important;
        width: 250px !important;
        max-width: 90%;
        min-width: 10% !important;
    }

    /* toggle form */
    @keyframes slide-form {
        from {
            padding: 0;
            height: 0;
            overflow: hidden;
        }
        to {
            height: 480px;
            overflow: hidden;
        }
    }

    .form-toggle form {
        animation-name: slide-form;
        animation-duration: 0.2s;
